/* RESPONSIVE CSS
-------------------------------------------------- */

/* SIGNIN PAGE */

.signin-body {
  margin-top: 33px;
  background-color: #f5f5f5;
  padding: 33px;
  max-width: 600px;
}


@media (min-width: 40em) {

  /* Bump up size of carousel content */
  .carousel-caption p {
    margin-bottom: 1.25rem;
    font-size: 1.25rem;
    line-height: 1.4;
  }

  .featurette-heading {
    font-size: 50px;
  }
}

@media (min-width: 62em) {
  .featurette-heading {
    margin-top: 7rem;
  }
}